import { Component, ContentChildren, ElementRef, Input, QueryList, ViewChild } from '@angular/core';
import { PreviewBlockDirective } from './preview-block.directive';

@Component({
  selector: 'c8y-preview-selection-frame',
  templateUrl: './preview-selection-frame.component.html'
})
export class PreviewSelectionFrameComponent {
  @Input()
  header: string;

  @Input()
  name: string;

  @Input()
  description: string;

  @Input('type')
  set _type(type: string) {
    this.type = type;
    this.showType = true;
  }

  @ViewChild('list')
  listRef: ElementRef<any>;

  @ContentChildren(PreviewBlockDirective)
  previewBlocks: QueryList<PreviewBlockDirective>;

  type: string;
  showType = false;
}
